<template>
  <div class="title-content">
    <div class="content">
      <el-row>
        <el-col>
          <div
            v-for="(item,index) in contentList"
            :key="index"
            class="title-item"
            @click="showContent(index,item)"
          >
            <a class="title-link">{{ item.title }}</a>
          </div>
        </el-col>
      </el-row>
    </div>
  </div>
</template>

<script>
import Input from "element-ui";
export default {
  name: "Home",
  data() {
    return {
      contentList: [
        // {
        //   title: "页面开发手册",
        //   contentShow: false
        // },
        // {
        //   title: "页面开发手册",
        //   contentShow: false
        // },
        // {
        //   title: "页面开发手册",
        //   contentShow: false
        // },
        // {
        //   title: "页面开发手册",
        //   contentShow: false
        // }
        // {
        //   title: "页面开发手册",
        //   contentShow: false
        // },
        // {
        //   title: "页面开发手册",
        //   contentShow: false
        // },
        // {
        //   title: "页面开发手册",
        //   contentShow: false
        // },
        // {
        //   title: "页面开发手册",
        //   contentShow: false
        // },
        // {
        //   title: "页面开发手册",
        //   contentShow: false
        // }
      ]
    };
  },
  mounted() {
    // fileRead();
    const articleList = require
      .context("@/assets/article", false, /.md$/)
      .keys();

    for (let i = 0; i < articleList.length; i++) {
      let article = {} 
      article.title =  articleList[i].replace(/.md$/, "").replace(/\.\//, "");
      this.contentList.push(article)
      // this.$set(contentList, i, article);
    }
  },
  methods: {
    fileRead() {
      const articleList = require
        .context("@/assets/article", false, /.md$/)
        .keys();

      for (let i = 0; i < articleList.length; i++) {
        this.contentList[i].title = articleList[i]
          .replace(/.md$/, "")
          .replace(/\.\//, "");
      }
    },
    showContent(index, item) {
      let showContentFlag = this.contentList[index].contentShow;
      this.contentList[index].contentShow = !!!showContentFlag;
      console.log(111);
      this.$router.push({
        path: "content",
        query: {
          title: item.title,
          index: index
        }
      });
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.title-content {
  border: 1px solid #b0e0e6;
  min-height: 75px;
  margin: 0 auto;
  max-width: 1200px;
  margin-bottom: 168px;
  scroll-behavior: smooth;
}

.title-item {
  height: 48px;
  text-align: left;
  font-size: 18px;
  border-top: 1px solid #b0e0e6;
  line-height: 48px;
  padding-left: 18px;
  cursor: pointer;
}

.title-item:first-child {
  border-top: none !important;
}

.title-item:hover {
  background-color: #b4eeb4;
}

.title-link {
  color: #ffa500;
  width: 100%;
}
</style>



