<template>
    <div>
        <div class="content">
            <el-row :gutter="10" >
                <el-col>
                    <vue-markdown :source="html" class="mark-down"></vue-markdown>
                </el-col>
                <!-- <div>{{ html }}</div> -->
            </el-row>
            <!-- <a href="/static/1.md" class="download" download="页面开发手册.md">下载</a> -->
        </div>
    </div>
</template>

<script>
import VueMarkdown from 'vue-markdown';
// import ARTICLE from '@/assets/article/页面开发手册.md'
import hljs from 'highlight.js'
import 'highlight.js/styles/idea.css'

const highlightCode = () => {
  const preEl = document.querySelectorAll('pre')

  preEl.forEach((el) => {
    hljs.highlightBlock(el)
  })
}
export default {
    name: 'Content',
    data() {
        return {
            articleShow: '',
            html:''
        }
    },
    beforeRouteEnter(to, from, next){
        next(vm => {
            console.log(to.query)
            let filePath = '@/assets/article/' + to.query.title + '.md' 
            let file =require('@/assets/article/' + to.query.title + '.md')
            // let reader = new FileReader();
            // reader.readAsText(file,'gbk')
            // reader.onload( result => {
            //     console.log(result)
            // })
            console.log(typeof(file))
            vm.html = file;
        })
    },
    mounted: function(){
        // let fso = new XMLHttpRequest("Scripting.FileSystemObject")
        // let s = fso.GetFolder("../article")
        highlightCode()
        // this.html = this.articleShow
    },
    updated () {
        highlightCode()
    },
    components: {
        VueMarkdown
    }

}
</script>

<style scoped>  
    .content {
        text-align: left;
        margin: 0 auto;
        max-width: 1200px;
        padding-bottom: 168px;
        scroll-behavior:smooth
    }

    .content .mark-down p {
        font-size: 20px;
    }

    .download {
        margin-top: 38px;
        height: 20px
    }
</style>

<style>
    .content h2 {
        font-size: 1.2em;
    }

    /* pre code {
        white-space: normal;
        word-break: break-all;
    } */
</style>





