import Vue from 'vue'
import Router from 'vue-router'
import Home from '../components/Home'
import Content from '../components/Content'
import Demo from '../components/Demo'
Vue.use(Router)

export default new Router({
  routes: [
    // {
    //   path: '/',
    //   name: 'HelloWorld',
    //   component: HelloWorld
    // },
    {
      path:'/',
      name:'Home',
      component: Home
    },
    {
      path:'/content',
      name:'Content',
      component: Content
    },
    {
      path: '/demo',
      name: 'Demo',
      component: Demo
    }
  ]
})



