<template>
  <div id="app">
    <!-- <Header></Header> -->
    <el-container>
        <el-header>
          <el-row>
            <el-col :span="24">
                <div class="header" @click="goHome()">
                    <p class="title">Stay hungry,stay foolish.</p>
                </div>
            </el-col>
        </el-row>
        </el-header>
      <el-main>
        <router-view/>
      </el-main>
    </el-container>
  </div>
</template>

<script>
import { Container,Header,Aside,Main,Footer,Row, Col} from 'element-ui'
// import myHeader from "./components/header.vue"
export default {
  name: 'App',
  methods:{
    goHome(){
        this.$router.push('/')
    }
  },
  components:{Header}
}

</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}

.header {
    height: 60px;
    background: #98F5FF;
  }

.title {
    margin-left: 26px;
    text-align: left;
    line-height: 63px;
    font-size: 18px;
    color: black;
}

p { margin:0;padding:0;border:0;outline:0; }

#app .el-header {
  padding: 0;
  height: 100%;
}
</style>



